.theme-switch {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  margin-left: auto;
  position: relative;
}

.theme-switch:before {
  content: "";
  cursor: pointer;
  width: 40px;
  height: 20px;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 20px;
  padding: 1px;
  transition: all .3s;
  display: block;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.theme-switch:after {
  content: "";
  cursor: pointer;
  width: 18px;
  height: 18px;
  background-image: url("switch-texture.e49cccd4.svg"), linear-gradient(#fff 0%, #f7f7f7 100%);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  border-radius: 50%;
  transition: all .3s;
  display: block;
  position: absolute;
  top: 1px;
  left: 1px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

.theme-switch:checked:before {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  transition: all .3s;
}

.theme-switch:checked:after {
  transform: translateX(113%);
}

